import i18n from '@/i18n'

function checkICOChecksum (ico) {
  const n = ico.split('').map(v => v * 1)
  return ico[7] * 1 === (11 - (8 * n[0] + 7 * n[1] + 6 * n[2] + 5 * n[3] + 4 * n[4] + 3 * n[5] + 2 * n[6]) % 11) % 10
}

const e = v => v == null || v === ''
const t = (key, ...args) => i18n.t(`validation-rules.${key}`, ...args)

const rules = {
  required: v => !e(v) || t('required'),
  nonnegative: v => e(v) || (!isNaN(v) && v >= 0) || t('nonnegative'),
  integer: v => e(v) || (!isNaN(v) && v % 1 === 0) || t('integer'),
  email: v => e(v) || (/^[^\s]+$/.test(v) && /^[^@]+@[^@]+\.[^@.]{2,}$/.test(v)) || t('email'),
  ico: v => e(v) || (/^\d{8}$/.test(v) && checkICOChecksum(v)) || t('ico'),
  phoneNumber: v => e(v) || (/^\+?[0-9 -]+$/.test(v)) || t('phone-number'),
  minLength: min => v => e(v) || ((v + '').length >= min) || t('min-length', { min }),
  maxLength: max => v => e(v) || ((v + '').length <= max) || t('max-length', { max }),
  past: v => e(v) || (new Date(v) < Date.now()) || t('past')
}

export default rules
